import { useContext, useEffect, useState } from "react";
import NavbarStyle from "./Navbar.module.css";
import { Link } from "react-router-dom";
import UserContext from "../context/UserContext";
import axios from "axios";
import Swal from "sweetalert2";

function Navbar() {
  const { userId, userInfo, cartItems, fetchLoggedInUser } =
    useContext(UserContext);
  const initialRole = userInfo?.role || "user";

  const [role, setRole] = useState(initialRole);
  useEffect(() => {
    if (userInfo?.role) {
      setRole(userInfo.role);
    }
  }, [userInfo]);

  const handleRoleSwitch = async () => {
    try {
      const res = await axios.patch(`/users/${userId}/switch-role`);
      setRole(res.data.newRole);
       Swal.fire({
                icon: "success",
                title: `Switched to ${res.data.newRole}`,
                text: "Welcome",
              });
      fetchLoggedInUser();
    } catch (err) {
      alert("Error switching role");
      console.error(err);
    }
  };

  return (
    <>
      <nav className={NavbarStyle.Nav}>
        <div className={NavbarStyle.nav_container}>
          <div className={NavbarStyle.logo_section}>
            <p className={NavbarStyle.company_name}> e-Shop</p>
            <button onClick={handleRoleSwitch} className={NavbarStyle.button}>
              Switch to {role === "admin" ? "User" : "Admin"}
            </button>
          </div>
          <div className={NavbarStyle.Navbar}>
            <Link to="/" className={NavbarStyle.li}>
              <i class="fa-solid fa-house"></i> Home
            </Link>

            <Link to="/dashboard-page" className={NavbarStyle.li}>
              <i class="fa-solid fa-gauge"></i> Dashboard
            </Link>
            <Link to="/cart-page" className={NavbarStyle.li}>
              <i class="fa-solid fa-cart-shopping"></i>{" "}
              {cartItems && cartItems.length > 0 && (
                <span className={NavbarStyle.cartNotification}>
                  {cartItems.length}
                </span>
              )}{" "}
              Cart
            </Link>

            {!userId ? (
              <Link to="/login" className={NavbarStyle.li}>
                <i class="fa-solid fa-user"></i> Signin
              </Link>
            ) : null}

            {!userId ? (
              <Link to="/SignUp" className={NavbarStyle.li}>
                <i class="fa-solid fa-user-plus"></i> Signup
              </Link>
            ) : null}
            {userId ? (
              <Link to="/logout" className={NavbarStyle.li}>
                <i class="fa-solid fa-user-plus"></i> Logout
              </Link>
            ) : null}
            {userInfo && userInfo.role === "admin" && (
              <Link to="/admin-page" className={NavbarStyle.li}>
                <i class="fa-solid fa-user-plus"></i> Admin
              </Link>
            )}
          </div>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
