import styles from './FooterPage.module.css';

const FooterPage = () => {
  return (
    <div className={styles.bgGray900}>
      <div className={styles.maxW2xl}>
        <div className={styles.textCenter}>
          <h3 className={styles.text3xl}>Download our Ecommerce app</h3>
          <p>Buy what you want.</p>
          <div className={styles.flexCenter}>
            <div className={`${styles.flexItemsCenter} ${styles.border} ${styles.roundedLg} ${styles.px4} ${styles.py2} ${styles.w52} ${styles.mx2}`}>
              <img
                src="https://cdn-icons-png.flaticon.com/512/888/888857.png"
                className={styles.img} alt=''
              />
              <div className={styles.textLeft}>
                <p className={styles.textXs}>Download on</p>
                <p className={styles.textSm}>Google Play Store</p>
              </div>
            </div>
            <div className={`${styles.flexItemsCenter} ${styles.border} ${styles.roundedLg} ${styles.px4} ${styles.py2} ${styles.w44} ${styles.mx2}`}>
              <img
                src="https://cdn-icons-png.flaticon.com/512/888/888841.png"
                className={styles.img} alt=''
              />
              <div className={styles.textLeft}>
                <p className={styles.textXs}>Download on</p>
                <p className={styles.textSm}>Apple Store</p>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.footer}>
          <p className={styles.footerText}>© ProamodKumar, 2024.</p>
          <div className={styles.footerLinks}>
            <span className={styles.link}>About us</span>
            <span className={`${styles.link} ${styles.borderLeft}`}>Contact us</span>
            <span className={`${styles.link} ${styles.borderLeft}`}>Privacy Policy</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FooterPage;
