import HomeStyle from "../home/Home.module.css";
import { useContext, useState } from "react";
import axios from "axios";
import UserContext from "../context/UserContext";
import { useNavigate } from "react-router-dom";

const SearchBar = () => {
    const {setResults} = useContext(UserContext);
    const [query, setQuery] = useState('');
    console.log('query',query)
   const navigate = useNavigate()
    
   
  
   const handleSearch = async (e) => {
    e.preventDefault();
    if (!query.trim()) {
        alert('Please enter a search term!');
        return;
      }
    try {
      const response = await axios.get(`/search?query=${encodeURIComponent(query)}`);
      setResults(response.data);
      if (response.data) {
        navigate('/search-page');
      }
    } catch (error) {
      console.error('Error fetching search results', error);
    }
  };
  
  return (
    <div> <form onSubmit={handleSearch}>
    <div className={HomeStyle.search_container}>
      <div className={HomeStyle.searchBar}>
        <i className="searchIcon fa fa-search"></i>
        <input
          className={HomeStyle.input}
          type="text"
          value={query} 
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Search by name"
        />
      </div>
      <button type="submit" className={HomeStyle.search}>
        <i className="searchIcon fa fa-search"></i>
      </button>
      
    </div>
    </form></div>
  )
}

export default SearchBar